import React, { Component } from 'react'

class SendMessageForm extends Component {
  constructor(props) {
    super(props)

    this.state = {
      text: ''
    }
  }

  onSubmit = (e) => {
    e.preventDefault()
    this.props.onSubmit(this.state.text)
  }

  onChange = (e) => {
    this.setState({
      text: e.target.value
    })
    this.props.onChange()
  }

  render() {
    return (
      <form onSubmit={this.onSubmit}>
        <input type='text' placeholder='type your message' onChange={this.onChange} />
        <input type='submit' />
      </form>
    )
  }
}

export default SendMessageForm


