import React, { Component } from 'react'
import Chatkit from '@pusher/chatkit'

import MessageList from './MessageList'
import SendMessageForm from './SendMessageForm'
import TypingIndicator from './TypingIndicator'
import WhoIsOnline from './WhoIsOnline'

export default class ChatScreen extends Component {

  constructor(props) {
    super(props)

    this.state = {
      messages: [],
      currentUser: {},
      currentRoom: {},
      usersWhoAreTyping: []
    }
  }


  componentDidMount = () => {
    const chatManager = new Chatkit.ChatManager({
      instanceLocator: 'v1:us1:e2068def-9683-46dd-bc93-d325e7e677d3',
      userId: this.props.username,
      tokenProvider: new Chatkit.TokenProvider({
        url: 'http://localhost:3001/authenticate'
      })
    })


    chatManager
      .connect()
      .then(currentUser => {
        this.setState({ currentUser })
        return currentUser.subscribeToRoom({
          roomId: 15200487,
          messagesLimit: 100,
          hooks: {
            onNewMessage: message => {
              this.setState({
                messages: [...this.state.messages, message]
              })
            },
            onUserStartedTyping: user => {
              console.log(user.name + ' started typing')
              this.setState({
                usersWhoAreTyping: [...this.state.usersWhoAreTyping, user.name]
              })

            },
            onUserStoppedTyping: user => {
              console.log(user.name + ' stopped typing')
              this.setState({
                usersWhoAreTyping: this.state.usersWhoAreTyping.filter(
                  username => username !== user.name
                )
              })
            },
            onUserCameOnline: () => this.forceUpdate(),
            onUserWentOffline: () => this.forceUpdate(),
            onUserJoined: () => this.forceUpdate()


          }
        })
      })
      .then(currentRoom => {
        this.setState({ currentRoom })
      })
      .catch(error => {
        console.error('error', error)
      })
  }

  sendMessage = (text) => {
    this.state.currentUser.sendMessage({
      roomId: this.state.currentRoom.id,
      text
    })
  }

  sendTypingEvent = () => {

    this.state.currentUser.isTypingIn({ roomId: this.state.currentRoom.id })
      .catch(error => console.log(error))

  }


  render() {
    return (
      <div style={{ 
        display: 'flex',
        height: '100vh' }}>


        <div style={{
          width: '30%',
          background: 'tomato'
        }}>
          <h1>Welcome to the chat!</h1>
          <p>{this.props.username}</p>
          <h2>Who is online?</h2>
          <WhoIsOnline users={this.state.currentRoom.users} />
          {console.log(this.state.currentRoom.users)}
        </div>

        <div style={{
          display: 'flex',
          flexDirection: 'column',
          
        }}>
        <div style={{flex: 1}}><MessageList messages={this.state.messages} /></div>
        
        <TypingIndicator usersWhoAreTyping={this.state.usersWhoAreTyping} />
        <SendMessageForm onSubmit={this.sendMessage} onChange={this.sendTypingEvent} />
        </div>

        
          
      </div>
    )
  }
}



